.slick-initialized {
    .slick-slide {
        display: flex;
        outline: none;
    }
}

.slick-dots {
    bottom: 67px;

    li.slick-active{
        button:before {
            color: cyan;
        }
    }

}

.slick-dotted {
    &.slick-slider {
        margin-bottom: 0;
    }
}

.slider-arrows{
    &.slick-slider {
        margin-bottom: 90px;
    }

    .slick-dots {
        bottom: -55px;
    }
}


.slick-next:before, .slick-prev:before{
    content: "\f061";
    opacity: 1;
    color: #95e3f5;
    font-family: FontAwesome;
    font-size: 44px;

    &:hover{
        color: #27c3e6;
    }
}

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before{
    opacity: 1;
}

.slick-prev:before{
    content: "\f060";
}

.mfp-zoom-out-cur, .mfp-zoom-out-cur .mfp-image-holder .mfp-close{
    cursor: pointer;
}

.mfp-bg{
    background-color: #000;
    opacity: 0.9;
}

img.mfp-img{
    box-shadow: 0px 0px 100px 100px rgba(68,12,140,1);
    padding: 0;
}

.mfp-image-holder .mfp-close, .mfp-iframe-holder .mfp-close{
    color: #757575;
    position: absolute;
    right: -40px;
    top: -40px;
}
