.block-feedback {
  padding: 55px 0;
  &__header {
	font-family: Lora, serif;
	font-style: italic;
	text-align: center;
	font-size: 73px;
	margin-bottom: 42px;
  }

  &__row {
	display: flex;
	width: 80%;
	margin: 0 auto;
  }

  &__item {
	flex: 1;
	width: 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
  }

  &__item-header {
	background-repeat: no-repeat;
	background-position: 0 0;
	width: 443px;
	height: 315px;
	position: relative;
  }

  &__photo-count{
	color: white;
	position: absolute;
	top: 17px;
	right: 15px;
	border: 1px solid white;
	padding: 4px 9px;
	font-size: 13px;
	line-height: 1;
	border-radius: 5px;
	border-radius: 16px;
  }

  &__bottom-block{
	position: absolute;
	bottom: 55px;
	left: 40px;
  }

  &__date{
	color: $color-white;
	font-size: 12px;
	margin-bottom: 6px;
	text-transform: uppercase;
  }

  &__title{
	font-size: 32px;
	font-weight: 900;
	color: $color-white;
	line-height: 1;
  }

  &__text{
	padding: 0 60px;
	margin: 15px 0 37px 0;
	font-size: 16px;
	line-height: 1.2;
  }

}
