.block-offer {
  text-align: center;
  max-width: 930px;
  margin: 0 auto;
  padding: 55px 0;

  &__sup-header {
	color: $color-cyan;
	text-transform: uppercase;
	letter-spacing: 3px;
	font-size: 15px;
	margin-bottom: 12px;
  }

  &__header{
	font-family: 'Lora', serif;
	font-style: italic;
	font-size: 37px;
  }

  &__text{
	font-size: 22px;
	line-height: 1.3;
  }
}

