.top-line {
    background-color: $color-light-grey;

    &__wrapper {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;

        .sticky & {
            display: flex;
            flex-flow: row;
            justify-content: space-between;
            @extend .page__wrapper;
        }

    }

    .sticky & {
        position: fixed;
        width: 100%;
        z-index: 1;
        top: 0;
        left: 0;

    }

    &__logotip {
        background-image: url("../images/logotip-big.png");
        display: block;
        margin: 8px 0 17px 0;
        width: 288px;
        height: 115px;

        .sticky & {
            background-image: url("../images/logotip.png");
            width: 155px;
            height: 45px;
            margin: 0;
        }
    }

}

.top-menu {
    font-family: 'Lora', serif;
    height: 56px;
    width: 100%;
    display: flex;
    justify-content: center;
    &__link {
        border-bottom: 4px solid transparent;
        color: $color-grey;
        text-decoration: none;
        margin-left: 60px;
        height: 100%;
        @include flexbox();
        @include align-items(center);

        &_active, &:hover {
            color: $color-black;
        }

        &_active {
            border-bottom: 4px solid $color-cyan;
        }

    }

    .sticky & {
        justify-content: flex-end;
    }

}