.block-team {

    text-align: center;

    &__header {
        border-bottom: 1px solid rgba(255, 255, 255, 0.4);
        color: $color-white;
        font-family: 'Lora', sans-serif;
        font-size: 36px;
        font-style: italic;
        padding-bottom: 12px;
    }

    &__text {
        color: $color-white;
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
        margin: 0 auto;
        max-width: 930px;
        padding-bottom: 70px;
    }

    &__members {
        color: $color-white;
        padding-top: 20px;
        text-align: left;
        padding-bottom: 160px;
        @include flexbox();
        @include justify-content(space-between);
    }

    &__member {
        padding-right: 44px;
        @include flexbox();
        @include justify-content(left);

        &:last-child {
            padding-right: 0;
        }

    }

    &__photo {
        background-size: cover;
        height: 120px;
        width: 120px;
        outline: none;
        @include flex-shrink(0);
        @include border-radius(50%);
    }

    &__data {
        padding: 40px 0 0 27px;
    }

    &__title {
        font-weight: bold;
        margin-bottom: 7px;
    }

    &__subtitle {
        font-weight: 300;
        text-transform: uppercase;
        font-size: 12px;
        margin-bottom: 21px;
    }

    &__description {
        padding-left: 42px;
        position: relative;
        line-height: 1.4;
        margin-top: -4px;

        &::before {
            display: block;
            content: " ";
            background: url(../images/blockqoute.png) top left no-repeat;
            width: 24px;
            height: 13px;
            position: absolute;
            top: 4px;
            left: 0;
        }
    }

}