.block-popup {
    font-family: 'Exo 2', sans-serif;
    margin: 20px auto;
    max-width: 930px;
    padding: 20px;
    position: relative;
    width: auto;

    &__date {
        text-align: center;
        margin-bottom: 60px;
        @extend .block-feedback__date;
    }

    &__title {
        text-align: center;
        margin-bottom: 40px;
        @extend .block-feedback__title;
    }

    &__text{
        color: #88858b;
        text-align: center;
        line-height: 28px;
    }

    &__footer{
        text-align: center;
        margin-top: 90px;
    }

}