.block-about {

    text-align: center;

    &__header {
        color: $color-white;
        font-family: 'Lora', sans-serif;
        font-size: 72px;
        font-style: italic;

    }

    &__text {
        color: $color-white;
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
        margin: 0 auto;
        max-width: 930px;
        padding-bottom: 70px;
    }

}