.footer {
  padding: 40px 0;
  text-align: center;

  &__address {
	font-size: 22px;
	margin-bottom: 13px;
  }

  &__phone {
	font-size: 31px;
	font-weight: bold;
	margin-top: 30px;
  }

  &__prefix {
	color: $color-grey;
	padding-right: 5px;
  }

  &__socials {
	display: flex;
	justify-content: center;
	margin: 32px 0 100px 0;
  }

  &__social-link {
	align-items: center;
	border: 2px solid $color-cyan;
	border-radius: 50%;
	color: $color-cyan;
	display: flex;
	font-size: 23px;
	height: 72px;
	justify-content: center;
	margin: 0px 20px;
	text-decoration: none;
	width: 72px;

	&:hover {
	  border-color: $color-purple;
	  background-color: $color-purple;
	  color: $color-white;
	  transform: scale(1.2)
	}

  }

  &__copyright{
	border-top: 1px solid $color-light-grey;
	padding-top: 40px;
  }

}