.button {
    background: rgb(84, 223, 255); /* Old browsers */
    background: -moz-linear-gradient(top, rgba(84, 223, 255, 1) 0%, rgba(39, 194, 229, 1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(84, 223, 255, 1) 0%, rgba(39, 194, 229, 1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(84, 223, 255, 1) 0%, rgba(39, 194, 229, 1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    border-radius: 44px;
    color: $color-white;
    cursor: pointer;
    display: inline-block;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#54dfff', endColorstr='#27c2e5', GradientType=0); /* IE6-9 */
    font-size: 23px;
    font-weight: bold;
    line-height: 1;
    outline: none;
    padding: 28px 48px;
    text-align: center;
    text-decoration: none;

    &:hover {
        background: rgb(84, 222, 254); /* Old browsers */
        background: -moz-linear-gradient(top, rgba(84, 222, 254, 1) 0%, rgba(133, 75, 255, 1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(84, 222, 254, 1) 0%, rgba(133, 75, 255, 1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(84, 222, 254, 1) 0%, rgba(133, 75, 255, 1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#54defe', endColorstr='#854bff', GradientType=0); /* IE6-9 */
    }

    &:active {
        background: rgb(104, 25, 255); /* Old browsers */
        background: -moz-linear-gradient(top, rgba(104, 25, 255, 1) 0%, rgba(136, 74, 255, 1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(104, 25, 255, 1) 0%, rgba(136, 74, 255, 1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(104, 25, 255, 1) 0%, rgba(136, 74, 255, 1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#6819ff', endColorstr='#884aff', GradientType=0); /* IE6-9 */
    }

    &_mid {
        border-radius: 44px;
        color: #fff;
        cursor: pointer;
        display: inline-block;
        font-size: 15px;
        font-weight: bold;
        line-height: 1;
        padding: 12px 20px;
        text-align: center;

        &:after {
            content: '\2022 \2022 \2022';
            margin-left: 10px;
        }

    }

    &_transparent{
        background: transparent;
        color: #26c2e5;
        border: 1px solid #26c2e5;
        &:hover {
            background: transparent;
        }
    }

}