.block-skills{

  padding: 72px 0 100px;

  &__header{
	font-family: Lora,serif;
	font-style: italic;
	font-size: 72px;
	text-align: center;
	margin-bottom: 20px;
  }

  &__row{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-bottom: 32px;
  }

  &__image{
	width: 120px;
	height: 120px;
	display: block;
	border-radius: 50%;
	margin: 0 auto;
  }

  &__item{
	min-width: 280px;
	padding: 22px;
	flex: 0;
	text-align: center;
  }

  &__text{
	margin-top: 10px;
	font-size: 16px;
	line-height: 1.4;
  }

  .button{
	margin: 0 auto;
  }
  &__footer{
	text-align: center;
  }
}